import React, { useState } from 'react';
import Popup from '../../Components/StandAloneComponents/Popup/Popup';

const usePopUp = () => {
    const [show, setShow] = useState(false);

    const openPopup = (val) => {
        setShow(val);
    }


    const displayPopup = ({...rest}) => {
        const tempS = Object.keys(show).length > 0 ? show.open : show;
        return tempS
            ? <Popup
                title={rest.title || show.title || null}
                message={rest.message || show.message || null}
                message2={rest.message2 || show.message2 || null}
                action1={rest.action1 || show.action1 || null}
                action2={rest.action2 || show.action2 || null}
            />
            : null;
    }

    return {
        show,
        openPopup,
        displayPopup
    }

}

export default usePopUp;