import React, { useState, useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';

import useLocalStorage from '../../Hooks/useLocalStorage/useLocalStorage';
import useDispatchHook from '../../Hooks/useDispatchHook/useDispatchHook';
import usePopUp from '../../Hooks/usePopUp/usePopUp';

import GameDetails from './GameDetails/GameDetails';
import AddScores from './AddScores/AddScores';
import AddPlayersForm from './AddPlayersForm/AddPlayersForm';
import SelectAnonPlayer from './SelectAnonPlayer/SelectAnonPlayer';
import Title from '../../Components/StandAloneComponents/Title/Title';

import replaceIDandName from '../../helpers/replaceIDAndName/replaceIDAndName';
import generateDate from '../../helpers/generateDate/generateDate';

// import classes from './Game.module.scss';


const showDefault = {
    gameDetails: true,
    addPlayers: false,
    addscores: false,
    newGame: false,
    playerSelect: false
}

const Game = (props) => {
    const liveGame = useSelector(state => state.liveGame);
    const user = useSelector(state => state.user);

    const { readFromStorage, removeFromStorage } = useLocalStorage();
    const { initGameDispatch, resetLiveGameDispatch, commitNewGameDispatch } = useDispatchHook();
    const { openPopup, displayPopup, show } = usePopUp();
    const gameRef = useRef();

    const [showWhich, setWhich] = useState(showDefault);
    


    useEffect(() => {
        const hasExistingGame = readFromStorage();
        if (hasExistingGame) {      // game exists in local storage
            const parsed = JSON.parse(hasExistingGame);
            const date = generateDate(parsed.createdAt);

            if (parsed.loggedIn && !show) {

                if (user.token) {
                    if (parsed.loggedIn === user.user._id) {            // situation 4: user logged in && localstorage id matches
                        console.log(date);
                        openPopup({
                            open: true,
                            ...popup1(hasExistingGame, date)
                        });
                    } else if (parsed.loggedIn !== user.user._id) {      // situation 5: user logged in but localstorage id doesnt match
                        cancel();
                    }
                } else {
                    openPopup({
                        open: true,
                        ...popup2(hasExistingGame, date)
                    });
                }
            } else {
                if (user.token) {
                    gameRef.current = hasExistingGame;
                    openPopup({
                        open: true,
                        ...popup3(hasExistingGame, date)
                    });
                } else {
                    openPopup({
                        open: true,
                        ...popup1(hasExistingGame, date)
                    });
                }
            }
        } else {
            changeFromGame('newGame');
        }
    }, []);


    const cancel = () =>{
        removeFromStorage();
        changeFromGame('newGame');
    }

    const submitClick = (id) =>{
        const temp = replaceIDandName(gameRef.current, id, user.user);
        console.log(temp);
        commitNewGameDispatch(temp);
        changeFromGame('gameDetails');
    }

    const playerSelect = (id) => {
        setWhich(showDefault);
        props.history.push({
            pathname: '/player',
            state: {
                id
            }
        })
    }

    const changeFromGame = (id) => {
        const base = {
            gameDetails: false,
            addPlayers: false,
            addscores: false,
            newGame: false,
            playerSelect: false
        }
        setWhich({
            ...base,
            [id]: true
        })
    }

    const buildGameDetails = () => (
        <GameDetails
            liveGame={liveGame}
            addNewPlayers={() => changeFromGame('addPlayers')}
            playerSelect={playerSelect}
            addScores={() => changeFromGame('addscores')}
            newGame={() => changeFromGame('newGame')}
            history={props.history}
            user={user.token}
        />
    )

    const buildNewGame = () => (
        <AddPlayersForm
            title='Create New Game'
            liveGame={{}}
            playerSelect={playerSelect}
            onClose={() => setWhich(showDefault)}
            isNew
            close={() => changeFromGame('gameDetails')}
        />
    )

    const buildAddPlayersForm = () => (
        <AddPlayersForm
            title='Add Player'
            liveGame={liveGame}
            playerSelect={playerSelect}
            onClose={() => setWhich(showDefault)}
        />
    )

    const buildAddScores = () => (
        <AddScores
            players={liveGame.players}
            close={() => setWhich(showDefault)}
            user={user}
        />
    )

    /**
    * Game in localstorage is anonymous and user is anonymous || 
    * Game in localstorage has loggedIn ID which matches the users ID 
    */
    const popup1 = (hasExistingGame, date) => ({      // situation 1
        title: {
            label: 'Game in Progress',
            ttlType: 'sub'
        },
        message: `We found a game that was started on ${date}.`,
        message2: 'would you like to continue or start a new game?',
        action1: {
            label: 'Start New Game',
            type: 'default',
            click: () => {
                cancel();
                openPopup(false)
            }
        },
        action2: {
            label: 'Continue',
            type: 'blue',
            click: () => {
                initGameDispatch(JSON.parse(hasExistingGame));
                openPopup(false)
            }
        }
    })

    /**
     * found game in localstorage with loggedIn ID, but user has not logged in: prompt user to log in
     */
    const popup2 = (hasExistingGame, date) => ({     // situation 3
        title: {
            label: 'Slight problem...',
            ttlType: 'sub'
        },
        message: `We found a game that was started on ${date}, but you need to login to continue this game.`,
        message2: 'If the game belongs to you that is...',
        action1: {
            label: 'Start New Game',
            type: 'default',
            click: () => {
                cancel();
                openPopup(false);
            }
        },
        action2: {
            label: 'Login',
            type: 'darkBlue',
            click: () => {
                openPopup(false);
                props.history.push('/login')
            }
        }
    })

    /**
     * user logged in, found game in localstorage that doesnt have a loggedIn ID
     */
    const popup3 = (hasExistingGame, date) => ({
        title: {
            label: 'SO....',
            ttlType: 'sub'
        },
        message: `We found a game that was started on ${date}, but unsure if this is your game.`,
        message2: 'Would you like to check and continue this game?',
        action1: {
            label: 'Start New Game',
            type: 'default',
            click: () => {
                cancel();
                openPopup(false)();
            }
        },
        action2: {
            label: 'Continue',
            type: 'darkBlue',
            click: () => {
                gameRef.current = hasExistingGame;
                openPopup(false);
                changeFromGame('playerSelect');
            }
        }
    })

    const buildPlayerSelect = () =>(
        <SelectAnonPlayer 
            game={JSON.parse(gameRef.current)}
            cancelClick={cancel}
            submitClick={submitClick}
        />
    )


    return (
        <div>
            <Title ttlType='section' label='Bowling Scores: Play' />
            {displayPopup()}
            {showWhich.gameDetails && buildGameDetails()}
            {showWhich.newGame && buildNewGame()}
            {showWhich.addPlayers && buildAddPlayersForm()}
            {showWhich.addscores && buildAddScores()}
            {showWhich.playerSelect && buildPlayerSelect()}

        </div>
    )
}

export default Game;